.button{
    color: var(--white);
    font-size: var(--buttonTextMediumDimension);
    padding: 0.5rem 1rem;
    background-color: var(--primaryColor);
    margin: .5rem;
    vertical-align: middle;
    display: inline-block;
    text-align: center;
    border-radius: 3px;
    font-family: var(--alternativeFont), sans-serif;
    font-weight: 700;
    cursor: pointer;
    transition: all var(--timing);

    &:hover{
        box-shadow: 0 2px 4px rgba(0,0,0,.25)
    }

    &--small{
        font-size: var(--buttonTextSmallDimension);
    }

    &--large{
        font-size: var(--buttonTextLargeDimension);
    }

    &--alt{
        background-color: transparent;
        color: var(--secondaryColor);
        border: .1em solid;
    }
}